"use client";

import { useState, useEffect } from "react";
import { useParams } from "next/navigation";
import { Heart, MessageCircle, Repeat } from "lucide-react";

type Reply = {
  id: number;
  username: string;
  avatar: string;
  time: string;
  content: string;
};

type Post = {
  id: number;
  username: string;
  avatar: string;
  time: string;
  content: string;
  likes: number;
  liked: boolean;
  replies: Reply[];
};

const postsData: Post[] = [
  {
    id: 1,
    username: "giahannndamne",
    avatar: "https://placehold.co/40x40",
    time: "4 gi·ªù",
    content: "Tym l√† 5s sau c√≥ ·∫£nh √° üòá",
    likes: 40,
    liked: false,
    replies: [
      {
        id: 2,
        username: "nguyenngocduy24032008",
        avatar: "https://placehold.co/40x40",
        time: "4 gi·ªù",
        content: "C√≥ kh",
      },
    ],
  },
];

const CommentPage = () => {
  const { postId } = useParams();
  const [post, setPost] = useState<Post | null>(null);
  const [newComment, setNewComment] = useState("");

  useEffect(() => {
    const foundPost = postsData.find((p) => p.id === Number(postId)) || null;
    setPost(foundPost);
  }, [postId]);

  if (!post) return <p>Loading...</p>;

  const handleComment = () => {
    if (!newComment.trim()) return;

    setPost((prev) => {
      if (!prev) return prev; // Tr√°nh l·ªói n·∫øu prev l√† null

      return {
        ...prev,
        replies: [
          ...prev.replies,
          {
            id: Date.now(),
            username: "User",
            avatar: "https://placehold.co/40x40",
            time: "V·ª´a xong",
            content: newComment,
          },
        ],
      };
    });

    setNewComment("");
  };

  return (
    <div className="max-w-lg mx-auto p-4 bg-white rounded-lg shadow-md">
      {/* Post ch√≠nh */}
      <div className="border-b pb-4">
        <div className="flex items-center gap-2">
          <img src={post.avatar} alt="avatar" className="w-10 h-10 rounded-full" />
          <div>
            <p className="font-semibold">{post.username}</p>
            <p className="text-sm text-gray-500">{post.time}</p>
          </div>
        </div>
        <p className="mt-2">{post.content}</p>
        <div className="flex gap-4 mt-2 text-gray-500">
          <span className="flex items-center gap-1">
            <Heart className="w-5 h-5" />
            {post.likes}
          </span>
          <span className="flex items-center gap-1">
            <MessageCircle className="w-5 h-5" />
            {post.replies.length}
          </span>
          <span className="flex items-center gap-1">
            <Repeat className="w-5 h-5" />
          </span>
        </div>
      </div>

      {/* Danh s√°ch comment */}
      <div className="mt-4">
        <p className="font-semibold">Thread tr·∫£ l·ªùi</p>
        {post.replies.map((reply) => (
          <div key={reply.id} className="flex items-start gap-2 mt-2">
            <img src={reply.avatar} alt="avatar" className="w-8 h-8 rounded-full" />
            <div>
              <p className="font-semibold">{reply.username}</p>
              <p className="text-sm text-gray-500">{reply.time}</p>
              <p className="mt-1">{reply.content}</p>
            </div>
          </div>
        ))}
      </div>

      {/* √î nh·∫≠p comment */}
      <div className="mt-4 flex gap-2 border-t pt-4">
        <input
          type="text"
          placeholder="Vi·∫øt b√¨nh lu·∫≠n..."
          className="flex-grow border p-2 rounded-lg"
          value={newComment}
          onChange={(e) => setNewComment(e.target.value)}
        />
        <button
          className="bg-blue-500 text-white px-4 py-2 rounded-lg disabled:opacity-50"
          onClick={handleComment}
          disabled={!newComment.trim()}
        >
          ƒêƒÉng
        </button>
      </div>
    </div>
  );
};

export default CommentPage;
